import React, { Component } from 'react'
import {
  LoadScript,
  GoogleMap,
  InfoWindow,
  Marker,
} from '@react-google-maps/api'
import { withRouter } from 'react-router-dom'
import { Image } from 'react-bootstrap'
import { baseURL } from '../../axios'

/*******************************************************************************
 * FindGameMap Component
 * Map component that contains all games which can be found by the user
 *******************************************************************************/

class FindGameMap extends Component {
  // Set the location of the map to the location provided
  constructor(props) {
    super(props)
    this.state = {
      redirect: false,
      mapPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
      markerPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
      role: [],
    }
  }

  //Handles redirecting the user to the game selected
  handleClick = (id) => {
    this.props.history.push(`/api/v1/games/${id}`)
  }

  render() {
    return (
      <LoadScript googleMapsApiKey={`AIzaSyBY-Mx2muSMsIf14UNilYTUo1MEkhpJPyY`}>
        <GoogleMap
          mapContainerStyle={{ width: '1100px', height: '400px' }}
          zoom={this.props.zoom}
          center={{
            lat: this.state.mapPosition.lat,
            lng: this.state.mapPosition.lng,
          }}
        >
          {/* Map through the list of games and display an InfoWindow for each game */}
          {this.props.otherGameInfo.map((game) => {
            return (
              <div>
                <InfoWindow
                  position={{
                    lat: game.location.coordinates[0] + 0.0018,
                    lng: game.location.coordinates[1],
                  }}
                >
                  <div
                    style={{
                      width: '200px',
                    }}
                  >
                    <Image
                      src={`${baseURL}/uploads/${game.photo}`}
                      style={{
                        maxWidth: '50px',
                        maxHeight: '50px',
                        float: 'left',
                      }}
                    />
                    <p onClick={() => this.handleClick(game.gameId)}>
                      {game.title}
                    </p>
                  </div>
                </InfoWindow>
                <Marker
                  draggable={false}
                  position={{
                    lat: game.location.coordinates[0],
                    lng: game.location.coordinates[1],
                  }}
                />
              </div>
            )
          })}
          <></>
        </GoogleMap>
      </LoadScript>
    )
  }
}
export default withRouter(FindGameMap)
