import React, { Component } from 'react'
import {
  LoadScript,
  GoogleMap,
  InfoWindow,
  Circle,
} from '@react-google-maps/api'
import { withRouter } from 'react-router-dom'
import { Image } from 'react-bootstrap'
import { baseURL } from '../../axios'

/*******************************************************************************
 * FindGroupMap Component
 * Map component that contains all groups which can be found by the user
 *******************************************************************************/

class FindGroupMap extends Component {
  // Set the location of the map to the location provided
  constructor(props) {
    super(props)
    this.state = {
      address: '',
      city: '',
      area: '',
      state: '',
      redirect: false,
      mapPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
      markerPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
      role: [],
    }
  }

  //Handles redirecting the user to the group selected
  handleClick = (id) => {
    this.props.history.push(`/api/v1/groups/${id}`)
  }
  render() {
    return (
      <LoadScript googleMapsApiKey={`AIzaSyBY-Mx2muSMsIf14UNilYTUo1MEkhpJPyY`}>
        <GoogleMap
          mapContainerStyle={{ width: '1100px', height: '400px' }}
          zoom={this.props.zoom}
          center={{
            lat: this.state.mapPosition.lat,
            lng: this.state.mapPosition.lng,
          }}
        >
          {/* Map through the list of groups and display an InfoWindow for each group */}

          {this.props.otherGroupInfo.map((group) => {
            return (
              <div key={group.groupId}>
                <InfoWindow
                  position={{
                    lat: group.location.coordinates[0] + 0.0018,
                    lng: group.location.coordinates[1],
                  }}
                >
                  <div
                    style={{
                      width: '200px',
                    }}
                  >
                    <Image
                      src={`${baseURL}/uploads/${group.photo}`}
                      style={{
                        maxWidth: '50px',
                        maxHeight: '50px',
                        float: 'left',
                      }}
                    />
                    <span onClick={() => this.handleClick(group.groupId)}>
                      {group.name}
                    </span>
                  </div>
                </InfoWindow>
                <Circle
                  name={'Dolores park'}
                  draggable={false}
                  defaultCenter={{
                    lat: group.location.coordinates[0],
                    lng: group.location.coordinates[1],
                  }}
                  radius={500}
                />
              </div>
            )
          })}
        </GoogleMap>
      </LoadScript>
    )
  }
}
export default withRouter(FindGroupMap)
